.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #c58363;
  padding: 50px;
  padding-bottom: 0;
  color: black;
  margin-top: 100px;
  width: 100%;
}

.footerSup {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 30px;
  border-bottom: 2px solid #eaeaea;
}

.datos ul {
  display: flex;
  width: 250px;
  justify-content: space-around;
}

.datos ul a {
  color: #eaeaea;
}

.datos ul li a:hover {
  color: #616161;
}
.datos ul li {
  font-size: 2.5rem;
}

.logoFooter img {
  width: 350px;
}

.copyright {
  border-top: 2px solid #eaeaea;
  margin: 0 auto;
}

@media (max-width: 576px) {
  .logoFooter img {
    display: none;
  }

  .datos {
    display: none;
  }

  .footer {
    margin: 0;
    padding: 0;
    width: 100vw;
  }

  .footerSup {
    padding: 0;
    margin: 0;
    display: flex;
  }

  .footerSup {
    border-bottom: none;
  }

  .copyright {
    border-top: none;
  }
}

@media screen and (min-width: 576px) and (max-width: 1400px) {
  .datos ul li {
    font-size: 2rem;
  }
  .logoFooter img {
    width: 250px;
  }
  .datosUtiles div i {
    font-size: 1.5rem;
  }
  .logoFooter img {
    display: none;
  }

  .datos {
    display: none;
  }

  .footer {
    margin: 0;
    padding: 0;
    width: 100%;
    overflow-x: hidden;
  }

  .footerSup {
    padding: 0;
    margin: 0;
    display: flex;
  }

  .footerSup {
    border-bottom: none;
  }

  .copyright {
    border-top: none;
  }
}
