.paisajeBanner {
  width: 100%;
  height: 80vh;
  object-fit: cover;
}

.containerHero {
  position: relative;
  z-index: -1;
}
.centerBanner {
  position: absolute;
  z-index: 100;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: black;
}

.centerBanner h2 {
  font-size: 3rem;
  letter-spacing: 0.3em;
  width: 1150px;
  text-transform: uppercase;
}

.centerBanner button {
  position: relative;
  color: #eaeaea;
  background: #c58363;
  width: 250px;
  padding: 20px;
  font-family: "Roboto", sans-serif;
  font-size: 1.2rem;
}

@media screen and (max-width: 576px) {
  .paisajeBanner {
    object-fit: cover;
    width: 100%;
    height: 300px;
  }

  .centerBanner {
    width: 90%;
  }

  .centerBanner h2 {
    font-size: 1.7rem;
    width: 100%;
  }
  .centerBanner button {
    display: none;
  }
}

@media screen and (min-width: 576px) and (max-width: 1400px) {
  .centerBanner {
    width: 99%;
  }

  .centerBanner h2 {
    font-size: 2rem;
    width: 100%;
  }
  .centerBanner button {
    width: 200px;
    height: 60px;
    font-size: 1rem;
    margin-top: 20px;
  }
}
