.aromas {
  margin-top: 50px;
  margin-bottom: 100px;
}

.aromasLista {
  display: flex;
  width: 60%;
  margin: 0 auto;
  justify-content: space-between;
}

.aromasLista ul {
  display: flex;
  flex-direction: column;
  padding: 0;
}
.aromasLista ul li {
  text-align: left;
  margin-bottom: 20px;
  font-size: 1.1rem;
}

.imagesAromas {
  width: 68%;
  display: flex;
  justify-content: space-between;
  margin: 100px auto;
}

.imagesAromas img {
  width: 400px;
  height: 400px;
  object-fit: cover;
}

@media screen and (max-width: 576px) {
  .aromasLista {
    width: 80%;
  }
  .aromasLista ul li {
    width: 60%;
    align-items: center;
  }

  .imagesAromas {
    display: flex;
    flex-direction: column;
    width: 80%;
    align-items: center;
  }
  .imagesAromas img {
    margin-bottom: 20px;
    width: 100%;
  }

  .textImgCenter h2 {
    font-size: 2.2rem;
  }
}

@media screen and (min-width: 576px) and (max-width: 1400px) {
  .imagesAromas {
    display: flex;

    width: 80%;
    align-items: center;
  }
  .imagesAromas img {
    width: 30%;
    margin-bottom: 20px;
  }
}
