.titleDetail {
  color: rgb(18, 18, 18);
  font-weight: bold;
  text-transform: uppercase;
}

.priceDetail {
  color: #f2b179;

  bottom: 50px;
  font-size: 1.4rem;
}

.descriptionDetail {
  color: #616161;
}

.itemDetail {
  display: flex;
  width: 100%;
  position: relative;
}

.itemDetail div {
  width: 100%;
}

.imageStock {
  width: 100%;
  height: 800px;
  object-fit: cover;
  z-index: -1;
}

.cardDetail {
  width: 500px !important;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-bottom: 20px;
  position: absolute;
  background-color: #fff;
  top: 200px;
  left: 200px;
  padding: 80px 50px;
  text-align: left;
  border: 2px solid #616161;
  border-radius: 20px;
}
.OfertaDetail {
  text-transform: uppercase;
  color: #f2b179;
  font-weight: bold;
}

.botonItemDetail {
  left: 0;
  bottom: 0;
  padding: 20px 30px;
  text-transform: uppercase;
  background-color: #c58363;
  color: #eaeaea;
  margin: 20px;
  width: 200px;
  cursor: pointer;
  transition: 0.2s;
}

.botonItemDetail:hover {
  background-color: #ffff;
  border: 1px solid #1a161a;
  color: #1a161a;
}

.botonesDetail {
  display: flex;
}

.btnBlanco {
  background-color: #fff;
  color: #1a161a;
}

.infoLugar {
  display: flex;
  flex-direction: column;
  background-color: #e0dfe0;
  padding-top: 300px;
  padding-bottom: 200px;
}

.datosLugar {
  display: flex;
  margin-bottom: 100px !important;
  margin-top: 100px !important;
}

.imgInfo img {
  width: 600px;
  height: 400px;
  object-fit: cover;
}

.titleLugar {
  display: flex;
  flex-direction: column;
  align-items: left;
  text-align: left;
}

.datosLugar div h2 {
  text-transform: uppercase;
}

.datosLugar div p {
  width: 80%;
}

.textRight div {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.textRight div p {
  text-align: right;
}

@media (max-width: 700px) {
  .imageStock {
    height: 40vh;
    width: 100vw;
  }
}

@media (min-width: 700px) and (max-width: 1400px) {
  .itemDetail {
    display: flex;

    margin: 0 auto;
    text-align: left;
    background-color: #ffff;
  }

  .imgInfo img {
    width: 400px;
    height: 400px;
  }

  .imageStock {
    height: 80vh;
  }
}
