.textImgCenter {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
  margin-top: 100px;
}

.textImgCenter h2 {
  position: absolute;
  font-size: 3rem;
  letter-spacing: 0.3em;
}

.imgBg {
  width: 200px;
  opacity: 0.1;
  z-index: -1;
}

@media screen and (max-width: 576px) {
  .textImgCenter {
    margin-bottom: 80px;
  }
  .textImgCenter h2 {
    font-size: 1.5rem !important;
  }
  .imgBg {
    width: 150px;
  }
}
@media screen and (min-width: 576px) and (max-width: 1400px) {
  .textImgCenter h2 {
    font-size: 2rem;
  }
}
