.contactLink {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 80%;
  margin-bottom: 300px;
}

.contactLink div {
  width: 33%;
}

.contactLink div h2 {
  font-size: 1rem;
}

.contactLink div a {
  font-size: 1.5rem;
  color: black;
}

@media screen and (max-width: 576px) {
  .contactLink {
    flex-direction: column;
    width: 90%;
    align-items: center;
    margin-bottom: 90px;
  }
  .contactLink div {
    flex-direction: column;
    width: 90%;
    align-items: center;
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 576px) and (max-width: 992px) {
}
