.containerItem .card {
  width: 400px;
  height: 600px;
  overflow: hidden;
  margin: 20px;
  text-align: center;
  transition: all 0.25s;
  position: relative;
  margin-bottom: 50px;
  margin-top: 50px;
}

.containerItem .card img {
  width: 400px;
  height: 400px;
  object-fit: cover;
  object-position: inherit;
}

.itemList {
  width: 90%;
  display: flex;
  justify-content: center;
  margin: auto;
  flex-wrap: wrap;
}

.title {
  display: flex;
  align-content: flex-start;
  justify-content: center;
  text-transform: uppercase;
  margin-top: 50px;
}

.infoItem {
  color: #1a161a;
}

@media (max-width: 700px) {
  .containerItem {
    display: flex;
    justify-content: center;
  }
  .containerItem .card {
    width: 80%;
    height: 500px;
    margin: 0;
    align-items: center;
  }

  .containerItem .card img {
    width: 100%;
    height: 300px;
  }
  .title {
    font-size: 1.2rem;
  }
  .button {
    width: 200px;
    font-size: 0.8rem;
  }
}

@media (min-width: 700px) and (max-width: 1400px) {
  .containerItem .card {
    width: 430px;
    height: 500px;
  }

  .containerItem .card img {
    width: 350px;
    height: 320px;
  }
  .title {
    font-size: 1.2rem;
  }
  .button {
    font-size: 1rem;
  }
}
