@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Roboto&display=swap");
@import url("./components/ItemDetail/ItemDetail.css");
@import url("./components/NavBar/navBar.css");
@import url("./components/BannerPromocional/BannerPromocional.css");
@import url("./components/Footer/Footer.css");

* {
  text-decoration: none;
  list-style: none;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
}

.container90 {
  width: 90%;
  margin: 0 auto;
}

.container60 {
  width: 60%;
  margin: 0 auto;
}

.container40 {
  width: 40%;
  margin: 0 auto;
}

body {
  width: 100%;
  margin: 0 auto;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
