.aboutUs {
  width: 60%;
  margin: 0 auto;
  margin-top: 100px;
}

.aboutUs div p {
  font-size: 1.3rem;
}

@media (max-width: 700px) {
  .aboutUs {
    width: 75%;
    text-align: center;
    margin-bottom: 100px;
  }
  .aboutUs div p {
    font-size: 0.9rem;
    width: 100%;
  }
}
