.promo {
  width: 100%;
  padding-bottom: 150px;
  position: relative;
  padding-top: 100px;
}

.logoPromo {
  width: 300px;
  margin-bottom: 30px;
}

.characteristics {
  display: flex;
  justify-content: space-around;
  position: relative;
  overflow-x: hidden;
}
.characteristics div {
  display: flex;
  justify-content: center;
  position: relative;
}

.characteristicsImg {
  width: 300px;
  object-fit: cover;
  opacity: 20%;
}

.characteristics h3 {
  font-size: 1.6rem;
  position: absolute;
  width: 500px;
  top: 40%;
}

@media screen and (max-width: 576px) {
  .logoPromo {
    width: 130px;
  }
  .characteristicsImg {
    width: 150px;
  }
  .characteristics {
    flex-direction: column;
  }
  .characteristics h3 {
    font-size: 1.2rem;
    width: 70%;
  }
}

@media screen and (min-width: 576px) and (max-width: 1400px) {
  .logoPromo {
    width: 250px;
  }

  .characteristics h3 {
    font-size: 1.5rem;
  }
  .characteristicsImg {
    width: 250px;
  }
}
