.nav_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #303030a4;
  padding: 0 5%;
  margin: 0 auto;
  position: fixed;
  width: 90%; /*poruqe tengo que poner 90% y no 100% para que se vea bien*/
  height: 120px;
  z-index: 100;
  top: 0;
}

.nav_item {
  margin: 10px;
  color: #fff;
}

.logo_navBar {
  margin: 10px;
  height: 40px;
}

.nav_menu {
  display: flex;
  align-items: center;
}

.nav_menu a i {
  display: none;
}

.activa {
  position: relative;
}
.activa::after {
  content: "◆";
  position: absolute;
  display: flex;
  flex-direction: column;
  left: 40%;
}

.inactiva {
  color: #e6e3dc;
}

.logoNavBar {
  width: 100px;
  margin: 10px;
}

.prueba {
  display: flex;
  align-items: center;
}

.solid {
  background-color: #c58363 !important ;
  transition: all 1s;
}

.nav_label,
.nav_input {
  display: none;
}

@media screen and (max-width: 576px) {
  .nav_container {
    height: 60px;
  }

  .nav_label {
    /* PROPIEDADES DEL LABEL DE MENU DESPLEGABLE */
    display: block;
    cursor: pointer;
  }
  .nav_label img {
    position: absolute;
    /* PROPIEDADES DEL ICONO DE MENU DESPLEGABLE */
    right: 20px;
    top: 8px;
    width: 50px;
  }
  .nav_menu {
    /* PROPIEDADES DEL MENU DESPLEGABLE */
    position: absolute;
    top: 60px;
    left: 0px;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: #f2b179;
    align-items: flex-start;
    padding: 30px;
    clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
    transition: clip-path 0.5s ease-in-out;
    z-index: 900;
  }
  .nav_menu {
    z-index: 100;
  }
  .nav_input:checked + .nav_menu {
    /* PROPIEDADES PARA QUE FUNCIONE EL MENU */
    clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
  }
}

@media screen and (min-width: 576px) and (max-width: 1400px) {
  .nav_container {
    height: 100px;
    font-size: 0.9rem;
  }
}
